import React, {Component} from 'react'
import Modal from 'react-modal';
import UploadPictures from '../UploadPicture/UploadPicture';
import {connect} from 'react-redux'

class UploadModal extends Component {
    constructor() {
      super();
      this.state = {
        showModal: false
      };
  
      this.handleOpenModal = this.handleOpenModal.bind(this);
      this.handleCloseModal = this.handleCloseModal.bind(this);
      
    }
    state = {
      img: '',
      location: '',
      date: '',
      voice_clip: ''
    }

    handleInputChangeFor = propertyName => (event) => {
      this.setState({
        [propertyName]: event.target.value,
      });
    }
   
    handleOpenModal() {
      this.setState({ showModal: true });
    }
  
    handleCloseModal() {
      this.setState({ showModal: false });
    }

    handleSubmit=()=> {
        console.log('hello from the upload btn');
        this.props.dispatch({type: 'UPLOAD'});

    }
  
    render() {
      return (
        <div>
          <button onClick={this.handleOpenModal}>Upload Picture</button>
          <Modal
            isOpen={this.state.showModal}
            contentLabel="Minimal Modal Example"
          >
            <div>
              <h2>Upload</h2>
              Select your memory here <UploadPictures onChange ={this.handleInputChangeFor}/>
              Always remember this place <input placeholder="location" onChange ={this.handleInputChangeFor}/>
              On this day <input placeholder="date" onChange ={this.handleInputChangeFor}/>
              Secure this moment <input placeholder="voice recorder will go here" onChange ={this.handleInputChangeFor}/>
            </div>
            <br></br>
            <div>
              <button onClick= {this.handleSubmit}>Upload</button>
            <button onClick={this.handleCloseModal}>Cancel</button>
            </div>
          </Modal>
        </div>
      );
    }
}
  

  
const mapStateToProps = state => ({
  errors: state.errors,
});
  export default connect(mapStateToProps)(UploadModal);